<script>
// document.querySelector('.js-select').select2({
//   minimumResultsForSearch: Infinity
// });
import DefaultPage from '@/components/DefaultPage'
import DateRangePicker from 'vue2-daterange-picker'
//you need to import the CSS manually
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css'

let today = new Date()
today.setHours(0, 0, 0, 0)

let yesterday = new Date()
yesterday.setDate(today.getDate() - 1)
yesterday.setHours(0, 0, 0, 0);

export default {



	name: 'orders',

	components: {
		DefaultPage,
		DateRangePicker
	},

	filters: {
		date (value) {
			if (!value)
				return ''
			let options = {year: 'numeric', month: 'numeric', day: 'numeric'};
			return Intl.DateTimeFormat('ru-RU', options).format(value)
		}
	},

	data() {
		return {
			dateRange: {
				startDate: '2020-12-10',
				endDate: '2020-12-20',
			},
			single_range_picker: true,
			show_ranges: false,
			singleDatePicker: false,
			timePicker: false,
			timePicker24Hour: false,
			showDropdowns: false,
			autoApply: false,
			showWeekNumbers: false,
			linkedCalendars: true,
			alwaysShowCalendars: true,
			appendToBody: false,
			closeOnEsc: true,
			opens: 'center',
			minDate: '2019-05-02 04:00:00',
			maxDate: '2021-12-26 14:00:00',
			rangesData: {
				'': [today, today],
				'': [yesterday, yesterday],
				' ': [new Date( today.getFullYear(), today.getMonth(), 1), new Date( today.getFullYear(), today.getMonth(), 30)],
				' ': [new Date(today.getFullYear(), 0, 1), new Date(today.getFullYear(), 11, 31)],
				' ': [new Date(today.getFullYear(), today.getMonth() - 1, 1), new Date(today.getFullYear(), today.getMonth(), 0)],
			},
			localeData: {
				firstDay: 1,
				format: 'dd-mm-yyyy',
				separator: ' - ',
				applyLabel: '',
				cancelLabel: '',
				weekLabel: '',
				daysOfWeek: ['', '', '', '', '', '', ''],
				monthNames: ['', '', '', '', '', '', '', '', '', '', '', ''],
			},

			pageOptions: {
				name:"orders",

				components: {
					menuAct: false,
					content: true,
					breadcrumbs: true
				}
			}
		}
	},

	methods: {
		filterSelect(event) {
			event.currentTarget.parentElement.children.forEach(btn=> {
				btn.classList.remove("selected")	
			})
			event.currentTarget.classList.add("selected")
		},

		openTooltip(event) {
			const selectedRow = document.querySelector(".table-products__list--selected")
			const openTooltip = document.querySelector(".tooltip-orders.show")

			const allClasses = Array.from(event.currentTarget.nextElementSibling.classList)
			if (
					allClasses.some(elClass => elClass == "tooltip-orders")
					&& !allClasses.some(elClass => elClass == "show")
				) {
				// close all tooltip
				openTooltip?.classList.remove("show")
				selectedRow?.classList.remove("table-products__list--selected")
				// open need tooltip
				event.currentTarget.nextElementSibling.classList.add("show")
				event.currentTarget.parentElement.parentElement.classList.add("table-products__list--selected")
			} else {
				// close all tooltip
				openTooltip?.classList.remove("show")
				selectedRow?.classList.remove("table-products__list--selected")
			}
		}
	}

}
</script>